:root {
  --color-terracotta: #c8553d;
  --color-terracotta-dark: #a93c27;
  --color-terracotta-light: #e3735f;
  --color-charcoal: #0c0c0d;
  --color-parchment: #f5f2e9;
  --color-stone: #9c9990;
}

.hero {
  height: 100vh;
  min-height: 700px;
  color: var(--color-parchment);
  perspective: 1000px;
  align-items: center;
  display: flex;
  position: relative;
  overflow: hidden;
}

.hero-background {
  background-color: var(--color-charcoal);
  z-index: -1;
  position: absolute;
  inset: 0;
  overflow: hidden;
}

.hero-pattern {
  width: 100%;
  height: 100%;
  opacity: .3;
  background-image: linear-gradient(90deg, #c8553d08 1px, #0000 1px), linear-gradient(#c8553d08 1px, #0000 1px);
  background-size: 40px 40px;
  position: absolute;
  top: 0;
  left: 0;
}

.hero-glow {
  width: 70%;
  height: 70%;
  filter: blur(100px);
  opacity: .6;
  transform-origin: center;
  background: radial-gradient(circle, #c8553d1f 0%, #c8553d0d 30%, #0000 70%);
  animation: pulseGlow 8s ease-in-out infinite alternate;
  position: absolute;
  top: -20%;
  right: -20%;
}

@keyframes pulseGlow {
  0% {
    opacity: .4;
    transform: scale(1);
  }

  100% {
    opacity: .8;
    transform: scale(1.1);
  }
}

.hero-content {
  max-width: 1200px;
  width: 100%;
  transform-style: preserve-3d;
  will-change: transform;
  padding: 2rem;
}

.hero-name {
  letter-spacing: -.02em;
  transform-style: preserve-3d;
  font-size: clamp(3.5rem, 10vw, 7rem);
  font-weight: 900;
  line-height: 1;
  font-family: var(--font-secondary);
  text-transform: uppercase;
  white-space: nowrap;
  margin-bottom: 2rem;
  overflow: hidden;
}

.hero-letter {
  opacity: 0;
  text-shadow: 0 2px 4px #0000004d;
  will-change: transform, opacity;
  animation: letterFadeInUp .6s cubic-bezier(.16, 1, .3, 1) forwards;
  display: inline-block;
  transform: translateY(40px)rotateX(10deg);
}

@keyframes letterFadeInUp {
  0% {
    opacity: 0;
    transform: translateY(40px)rotateX(10deg);
  }

  100% {
    opacity: 1;
    transform: translateY(0) rotateX(0);
  }
}

.hero-title {
  color: #f5f2e9d9;
  opacity: 0;
  font-size: clamp(1.5rem, 5vw, 2.5rem);
  font-weight: 700;
  font-family: var(--font-primary);
  border-left: 3px solid var(--color-terracotta);
  margin-bottom: 2rem;
  padding-left: 1rem;
  transition: opacity .8s ease-out .3s, transform 1s ease-out .3s;
  transform: translateY(30px)translateZ(5px);
}

.hero-subtitle {
  max-width: 700px;
  color: #f5f2e9b3;
  opacity: 0;
  font-size: clamp(1rem, 3vw, 1.25rem);
  line-height: 1.6;
  font-family: var(--font-primary);
  margin-bottom: 3rem;
  transition: opacity .8s ease-out .6s, transform 1s ease-out .6s;
  transform: translateY(30px)translateZ(0);
}

.hero-buttons {
  gap: 1.5rem;
  margin-top: 2rem;
  display: flex;
  transform: translateZ(20px);
}

.btn {
  font-family: var(--font-secondary);
  text-transform: uppercase;
  letter-spacing: 1px;
  z-index: 1;
  cursor: pointer;
  border: 2px solid #0000;
  border-radius: 4px;
  align-items: center;
  padding: .75rem 1.75rem;
  font-size: 1rem;
  font-weight: 600;
  transition: all .3s;
  display: inline-flex;
  position: relative;
  overflow: hidden;
}

.btn:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background: #0000001a;
  transition: transform .3s;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
}

.btn:hover:before {
  transform: translateY(0);
}

.btn-primary {
  background: var(--color-terracotta);
  color: var(--color-parchment);
  box-shadow: 0 4px 12px #c8553d4d, inset 0 1px 1px #fff3;
}

.btn-primary:hover {
  background: var(--color-terracotta-dark);
}

.btn-secondary {
  border: 2px solid var(--color-terracotta);
  color: var(--color-parchment);
  background: #c8553d1a;
  box-shadow: 0 4px 12px #c8553d1a;
}

.btn-secondary:hover {
  border-color: var(--color-terracotta-light);
  background: #c8553d33;
}

.btn-icon {
  margin-left: .5rem;
  transition: transform .3s;
  display: inline-flex;
}

.btn:hover .btn-icon {
  transform: translateX(5px);
}

.scroll-indicator {
  color: var(--color-parchment);
  opacity: .7;
  flex-direction: column;
  align-items: center;
  animation: float 3s ease-in-out infinite;
  display: flex;
  position: absolute;
  bottom: 2rem;
  left: 50%;
  transform: translateX(-50%);
}

@keyframes float {
  0%, 100% {
    transform: translateX(-50%)translateY(0);
  }

  50% {
    transform: translateX(-50%)translateY(-10px);
  }
}

@media (prefers-reduced-motion: reduce) {
  .hero-glow, .scroll-indicator {
    animation: none;
  }

  .btn-primary:before {
    display: none;
  }

  .hero-content {
    transition: none !important;
    transform: none !important;
  }
}

@media (max-width: 768px) {
  .hero-content {
    padding: 1rem;
    transform: none !important;
  }

  .hero-name {
    white-space: normal;
    word-break: break-word;
    font-size: clamp(2.5rem, 8vw, 4rem);
  }

  .hero-buttons {
    width: 100%;
    max-width: 400px;
    flex-direction: column;
    gap: 1rem;
  }

  .btn {
    width: 100%;
    text-align: center;
  }
}

